import React from "react"
import Helmet from "react-helmet"
import HoldingPage from '../components/HoldingPage/HoldingPage'


const IndexPage = () => {
  return (
    <>
      <Helmet>
        <html lang="en" />
        <title>Cape Designs web development and graphic design</title>
        <meta name="description" content="Professional web design, development and graphic design services"></meta>

        <meta property="og:url" content="https://www.capedesigns.co"></meta>
        <meta property="og:type" content="website"></meta>
        <meta property="og:title" content="Professional web design, development and graphic design services"></meta>
        <meta property="og:image" content="https://www.capedesigns.co/images/ogimage"></meta>
        <meta property="og:image:width" content="1200"></meta>
        <meta property="og:image:height" content="630"></meta>
        <meta property="og:description" content="Professional web design, development and graphic design services"></meta>
        <meta property="og:site_name" content="Cape Designs"></meta>

        <meta name="twitter:card" content="summary_large_image"></meta>
        <meta name="twitter:site" content="@rkkcreative"></meta>
        <meta name="twitter:url" content="https://capedesigns.co"></meta>
        <meta name="twitter:title" content="Cape Designs web development and graphic design"></meta>
        <meta name="twitter:description" content="Professional web design, development and graphic design services"></meta>
        <meta name="twitter:image" content="https://www.capedesigns.co/images/ogimage"></meta>

        <script type="application/ld+json">{`
        {
            "@context": "http://schema.org",
            "@type": "LocalBusiness",
            "name": "Cape Designs",
            "description": "Professional web design, development and graphic design services",
            "image": "https://www.capedesigns.co/images/ogimage",
            "logo": "https://www.capedesigns.co/images/logo",
            "@id": "https://www.capedesigns.co",
            "url": "https://www.capedesigns.co",
            "telephone": "",
            "address": {
              "@type": "PostalAddress",
              "addressLocality": "Cape Town",
              "addressCountry": "za"
            },
            "openingHoursSpecification": {
              "@type": "OpeningHoursSpecification",
              "dayOfWeek": [
                "Monday",
                "Tuesday",
                "Wednesday",
                "Thursday",
                "Friday",
                "Saturday",
                "Sunday"
              ],
              "opens": "09:00",
              "closes": "23:59"
            },
            "sameAs": [
              "https://www.facebook.com/RKKCreative/",
              "https://twitter.com/rkkcreative/",
              "https://www.instagram.com/rkkcreative/",
              "https://www.pinterest.com/rkkcreative/",
              "https://www.linkedin.com/in/rkkcreative/
            ],
            "priceRange": "$"
        }
   `}</script>
      </Helmet>
      <HoldingPage />
    </>
  )
}

export default IndexPage
